<template lang="pug">
  .ui-filters-hourly-rate
    vue-slider(
      v-model="form.rate"
      :lazy="true"
      :min="min"
      :max="max"
      :min-range="10"
      tooltip="none"
      @dragging="updateUI"
      :enable-cross="false"
    )
    .is-flex.space-between
      span.h5.text-dark
        | {{ui.rates[0]}}
        small EUR
      span.h5.text-dark
        | {{ui.rates[1]}}
        small EUR

</template>

<script>
  import vueSlider from 'vue-slider-component'
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "FiltersHourlyRate",
    components: {
      vueSlider
    },
    props: [],
    data() {
      return {
        // UX
        form: {
          rate: this.$store.getters.filters.rate || gon.rates
        },
        min: gon.rates[0],
        max: gon.rates[1],
        ui: {
          rates:  gon.rates
        },
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal) {
          this.ui.rates = newVal.rate
          this.setFilters(newVal).then((res) => {
            this.getFilteredProducts(res)
          })
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['setFilters', 'getFilteredProducts']),
      updateUI(val) {
        this.ui.rates = val
      }
    },
  };
</script>


<style lang="scss">
  @import 'colors';
  $themeColor: $primary;
  @import '~vue-slider-component/lib/theme/default.scss';

</style>
