<template lang="pug">
  .products.px-3
    .row
      .col-3.column--filters

        .is-flex.mt-4
          h2.h2.d-inline-block Filters
          .ml-auto
            b-button.d-none.d-xxl-inline-block.btn-reset.py-0.px-2.mr-1(
              size="sm"
              variant="white"
              v-if="filteredProducts.length"
              @click="resetFilters();filtersVersion+=1"
              )
              i.mdi settings_backup_restore
            button-apply-filters.btn-sm(text="PRESS ENTER TO APPLY")

        //- FILTERS

        filters.mt-3.px-2

        //- CUSTOM FILTERS

        babysitter-custom-filters.px-2(:key="filtersVersion")

        //- SUBMIT BUTTON

        button-apply-filters.w-100.mt-auto.mb-2

      //- :class="{'is-loading': filteredProductsQuerying}"
      .col-9.px-3.light-bg-color.column--results(
        :class=`{
           'col-9': !favorites.length,
           'col-7': favorites.length
           }`
      )

        //- LISTING
        recycle-scroller(
          class="scroller"
          :items="products"
          :item-size="260"
          :buffer="1000"
          key-field="id"

        )
          template(v-slot:before)
            h2.mt-4.w-100.valign.mb-0
              span.h2
                transition(name="slide-up" mode="out-in")
                  span.mr-1(
                    :key="products.length"
                  ) {{products.length}}
                | babysitters
              b-button-group.ml-auto.mr-2
                b-button.ml-auto.valign(
                  @click="sortBy('rate.amount')"
                  variant="white"
                  size="sm"
                  )
                  i.mdi.is-size-5.mr-1 sort
                  | Sort by price

          template(v-slot="{ item }")
            product-list-item.card.my-3.mx-1(:product="item")

      .col-2.px-3.light-bg-color.column--favorites(v-if="favorites")


</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import ProductListItem from "../ui/product-list-item";
  import Filters from "../sections/filters";
  import BabysitterCustomFilters from "../sections/babysitter-custom-filters";
  import ButtonApplyFilters from "../ui/button-apply-filters";

  export default {
    name: "Products",
    components: {
      ButtonApplyFilters,
      BabysitterCustomFilters,
      Filters,
      ProductListItem
    },
    props: [],
    data() {
      return {
        // FORM
        filtersVersion: 1
      };
    },
    computed: mapGetters([
      'products',
      'filteredProducts',
      'filteredProductsQuerying',
      'favorites'
    ]),
    mounted() {
      if (!this.products.length) {
        this.getProducts()
      }
    },
    methods: mapActions([
      'resetFilters',
      'getProducts',
      'sortBy'
    ]),
  };
</script>


