<template lang="pug">
  .ui-product-list-item.is-flex
    .main.is-flex.p-4.flex-grow-1
      .product-picture.pr-2

        //- MAIN PICTURE

        .image(:style="product.attachment ? `background: url(${attachment})` : ''" v-if="product.attachment")

        //- RATINGS

      .product-info.px-2.has-fg-1

        //- NAME

        h2.is-flex.h4.valign.justify-content-start.text-dark
          | {{product.title}}
          i.flag-icon.ml-3(
            v-for="tagId in languages"
            :class="[`flag-icon-${tagsById[tagId].name.substring(3).toLowerCase()}`]"
            )

          //- FAVORITE BUTTON

          b-button.ml-auto.btn-favorite.scale-on-click(
            pill
            variant="white"
            @click="favorite ? removeFavorite(favorite) : addToFavorites()"
          )
            i.mdi.is-size-3(:class="{'text-danger': favorite}") {{ favorite ? 'favorite' : 'favorite_border' }}

        hr

        //- HOURLY RATE

        .is-flex.w-100.justify-content-start.mb-3
          .houry-rate.mr-5
            h3.h6 HOURLY RATE
            p.rate.h3.text-dark
              | {{product.rate.amount}}
              small {{product.rate.currency}}

          div(v-if="experience.length")
            h3.h6 EXPERIENCE
            p.experience.h5.text-dark
              | {{ $t(`tags.${tagsById[experience[0]].kind}.${tagsById[experience[0]].name}`)}}

        //- :variant="tagsById[tagId].color || 'white'"
        b-badge.tag.mr-1(
          pill
          v-for="tagId in tags"
          variant="dark"
        )
          | {{tagsById[tagId] ? $t(`tags.${tagsById[tagId].kind}.${tagsById[tagId].name}`) : ''}}

    .product-description.px-5.py-4.shadow

      p.is-size-6(v-html="product.description")

</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "ProductListItem",
    components: {
    },
    props: ['product'],
    data() {
      return {
        // FORM
      };
    },
    computed: {
      ...mapGetters([
        'attachmentsById',
        'tagsById',
        'favoritesByModel'
      ]),
      attachment() {
        if (!this.product.attachment) {
          return null
        } else if (process.env.NODE_ENV == 'development') {
          return `/uploads/${this.product.attachment}`;
        }
        return `/uploads/attachment/picture/${this.product.id}/${this.product.attachment}`
      },
      favorite() {
        return this.favoritesByModel[`Product-${this.product.id}`]
      },
      languages() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind == 'language')
      },
      experience() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind == 'experience')
      },
      tags() {
        return this.product.tags.filter(tagId => this.tagsById[tagId] && this.tagsById[tagId].kind != 'language' && this.tagsById[tagId].kind != 'experience')
      }
    },
    created() {
      // if (this.product.attachments.data[0]) {
      //   this.attachment = this.attachmentsById[this.product.attachments.data[0].id].attributes
      // }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['saveFavorite', 'removeFavorite']),
      addToFavorites() {
        this.saveFavorite({
          model_type: 'Product',
          model_id: this.product.id
        })
      },
    },
  };
</script>


