      height: 3rem;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      i:not(.text-red):hover {
        color: lighten(#d9534f, 20);
      }
    }

    .flag-icon {
      width: 1.5rem;
    }

    .houry-rate {
      min-width: 120px;
    }

    .experience {
      margin-top: .9rem;
    }

    .badge {
      padding: 0.5em 0.8em;
    }
    .badge-dark {
      background-color: $midGray;
      background-color: lighten($primary, 30);
      color: $darkGray;
    }

    .product-picture .image {
      width: 120px;
      height: 120px;
