<template lang="pug">
  .section--babysitter-custom-filters

    //- KIND

    h3.h4.is-flex.valign.clickable(@click="babysittings = !babysittings")
      | Babysitting
      span.ml-auto
        i.mdi {{ babysittings ? 'expand_more' : 'expand_less' }}
    tag-check-box-group(
      :appear-on-hover="true"
      :hovering="babysittings"
      kind="kind"
      v-model="form.kind"
    )

    //- EXPERIENCE

    //-filters-dropdown-for-checkbox(
      title="Experience"
      kind="experience"
      v-model="form.experience")
      /tag-check-box-group( :appear-on-hover="true" :hovering="experiences" kind="experience" v-model="form.experience" )

    h3.h4.is-flex.valign.clickable(@click="experiences = !experiences")
      | Experience
      span.ml-auto
        i.mdi {{ experiences ? 'expand_more' : 'expand_less' }}
    tag-check-box-group(
      :appear-on-hover="true"
      :hovering="experiences"
      kind="experience"
      v-model="form.experience"
    )

    //- AGE RANGES

    h3.h4.is-flex.valign.clickable(@click="ageRanges = !ageRanges")
      | Age of your kids
      span.ml-auto
        i.mdi {{ ageRanges ? 'expand_more' : 'expand_less' }}
    tag-check-box-group(
      :appear-on-hover="true"
      :hovering="ageRanges"
      kind="age_range"
      v-model="form.age_range"
    )

    //- QUALIFICATIONS

    h3.h4.is-flex.valign.clickable(@click="qualifications = !qualifications")
      | Qualification
      span.ml-auto
        i.mdi {{ qualifications ? 'expand_more' : 'expand_less' }}
    tag-check-box-group(
      :appear-on-hover="true"
      :hovering="qualifications"
      kind="qualification"
      v-model="form.qualification"
    )

    //- LANGUAGES

    h3.h4 Languages
    .is-flex
      language-selector(
        @select="addLanguage"
        :show-choose="true"
      )

      b-button.mr-2.is-flex.valign(@click="removeLanguage(lang)" v-for="lang in form.language" variant="white" size="sm")
        i.flag-icon.mr-2(:class="'flag-icon-'+lang.substring(3).toLowerCase()")
        i.mdi.text-grey remove_circle_outline


</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import {tagsOptions} from "@/config";
  import TagCheckBoxGroup from "../ui/tagCheckBoxGroup";
  import LanguageSelector from "../ui/language-selector";
  import debounce from 'lodash/debounce';
  import FiltersDropdownForCheckbox from "../ui/filters-dropdown-for-checkbox";

  export default {
    name: "BabysitterCustomFilters",
    components: {FiltersDropdownForCheckbox, LanguageSelector, TagCheckBoxGroup},
    props: [],
    data() {
      return {
        // UX
        isLoading: false,
        form: {
          kind: [],
          experience: [],
          qualification: [],
          age_range: [],
          language: []
        },
        babysittings: true,
        experiences: false,
        ageRanges: false,
        qualifications: false,
        ...tagsOptions
      };
    },
    computed: {
      ...mapGetters(['filters', 'filtersInternal']),
    },
    watch: {
      form: {
        deep: true,
        handler(newVal) {
          this.$localStorage.set('babysitterCustomFilters', newVal)
          this.setFilters(newVal).then((res) => {
            this.getFilteredProducts(res)
          })
        }
      }
    },
    localStorage: {
      babysitterCustomFilters: {
        type: Object
      }
    },
    created() {
      const previousFilters = this.$localStorage.get('babysitterCustomFilters')
      if (previousFilters) {
        this.form = Object.assign({}, this.form, previousFilters)
        console.log("NOW:: ", this.form)
      }
    },
    methods: {
      ...mapActions(['setFilters', 'getFilteredProducts']),
      addLanguage(language) {
        this.form.language.push(language)
      },
      removeLanguage(language) {
        this.form.language = this.form.language.filter(lang => lang != language)
      }
    },
  };
</script>


<style lang="scss">
  @import 'colors';

  .section--babysitter-custom-filters {
    position: relative;
    .h4 {
      margin-top: 1rem;
    }

    .open {
      position: absolute;
      background-color: #FFF;
      border-bottom: 1px solid #dedede;
      padding-bottom: 1rem;
      z-index: 10;
    }

    .custom-checkbox {
      width: 100%;
    }

    input:checked + label {
      color: $primary
    }

    .custom-control-label {
      cursor:     pointer;
      flex-grow: 1;

      &:hover {
        color: $primary;
      }
    }
  }
</style>
